body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 15s linear;
          animation: App-logo-spin infinite 15s linear;
  height: 60%;
}

.App-header {
  position: relative;
  background-color: #282c34;
  height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.serie-list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.serie-list__element {
    flex: 0 1 400px;
    max-height: 100px;
}

.serie-list__element--score {
    height: 100%;
    width: 30%;
}

.serie-list__element--score.pretty-bad .CircularProgressbar-path, .serie-list__element--score.pretty-bad .CircularProgressbar-text {
    stroke: #e74c3c;
    fill: #e74c3c;
}

.serie-list__element--score.pretty-good .CircularProgressbar-path, .serie-list__element--score.pretty-good .CircularProgressbar-text {
    stroke: #f1c40f;
    fill: #f1c40f;
}

.serie-list__element--score.excellent .CircularProgressbar-path, .serie-list__element--score.excellent .CircularProgressbar-text {
    stroke: #2ecc71;
    fill: #2ecc71;
}

.serie-list__element--title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}

.serie-list__element--title span.inline {
  display: inline;
  padding-right: 3px;
}
